<template>
    <div>
        <h1 class="divided">转盘</h1>
        <go-back />
         <div v-if="loading">
                <load-indicator loadText="正在加载中"></load-indicator>
        </div>
        <template v-if="!loading">
            <div v-if="result.info">
                <div class="section">
                    <div class="title">基本信息</div>
                    <h4>第{{result.info._Season}}季 {{result.info._title}}</h4>
                    <p>时间：{{result.info._StartDate}} ~ {{result.info._EndDate}}</p>
                    <p>等级：Lv{{result.info._MinLevel}} ~ {{result.info._MaxLevel}}</p>
                    <img :src="npcPic(result.info._Icon)" style="float:right">
                </div>
                <div class="section">
                    <div class="title">阶段奖励</div>
                    <table>
                        <thead>
                            <th style="width:200px;">阶段</th>
                            <th>奖励</th>
                        </thead>
                            <tr v-for="(v,k) in result.reward" :key="k">
                                <td>{{v.title}}</td>
                                <td>
                                    <div class="Title">
                                        <div v-for="(vv,kk) in v.reward" :key="kk" class="titleitem">
                                            <item-icon-tooltip
                                                :itemId="vv.itemId" 
                                                :itemStub="vv.item"
                                                :count="vv.count"
                                                :noLink="noLink">
                                            </item-icon-tooltip>
                                        </div>
                                    </div>
                                </td>
                            </tr>
                    </table>
                </div>
                <div class="section">
                    <div class="title">活动商店</div>
                    <shop-browser :shopId="shopId"  :useClick="useClick" />
                </div>
                <div class="section">
                    <div class="title">阶段怪物</div>
                    <table class="labryinth-table">
                        <thead>
                            <th style="width:200px;">怪物</th>
                            <th>阶段</th>
                            <th>血量</th>
                            <th>基础攻击</th>
                            <th>NPC</th>
                        </thead>
                            <tr v-for="(v,k) in result.marker" :key="k">
                                <td>{{v._MobName}}</td>
                                <td>{{v._Grade}}</td>
                                <td>{{v._HP}}</td>
                                <td>{{v._AttackValue}}</td>
                                <td><img :src="npcPic(v._Image)" style="height:150px;"></td>
                            </tr>
                    </table>
                </div>
            </div>    
            <div v-else>该活动目前无进行</div>
        </template>
        <back-to-top class="topButton"><i class="fa fa-arrow-up"></i></back-to-top>
    </div>
</template>
<script>
import Vue from 'vue';
import GameInfo from '../api/GameInfo';
import GoBack from "@/components/util/GoBack.vue";
import ItemIconTooltip from '@/old/items/ItemIconTooltip.vue';
import Loader from "@/components/util/Loader";
import BackToTop from '@/components/util/BacktoTop';
import ShopBrowser from '@/components/item/shop/ShopBrowser.vue';
Vue.component("load-indicator", Loader);
export default {
    created(){
        this.fetchData();
    },
    components: {
        GoBack,
        ItemIconTooltip,
        BackToTop,
        ShopBrowser
    },
    data(){
        return{
            result:null,
            noLink:false,
            loading:true,
            shopId:100004,
            useClick: true
        }
    },
    methods:{
        npcPic(file){
            return `https://reference.dn.divinitor.com/api/server/ko/dds/${file}/png`;
        },
        fetchData(){
            if(GameInfo.boardgameCache){
                this.result = GameInfo.boardgameCache;
                this.loading = false;
            }else{
            GameInfo.Boardgame(this.$store.state.regionCode,
                    (res) => {
                        this.result = res;
                        this.loading = false;
                    },
                    (err) => {
                        this.error = err;
                        this.loading = false;
                    });
            }
        }
    }
}
</script>
<style lang="less" scoped>
@import "../../less/core.less";
.Title {
    display: flex;
    flex-direction: row;

    .titleitem {
        flex: 0 0 auto;
    }
}

.labryinth-table{
    margin:0 auto;
}

.section {
        position: relative;
        margin-top: 35px;
        border-top: 1px solid @dv-c-accent-1;
        padding-top: 4px;
        .title {
            font-size: 12px;
            color: @dv-c-accent-1;
            text-transform: uppercase;
            letter-spacing: 0.2em;
            position: absolute;
            top: -1.3em;
            .left(0);
            user-select: none;
        }
        button{
            margin-left:0.6em;
            margin-top:0.6em;
            padding: 10px 15px;
            height:50px;
            border: 1px solid #3B6E96;
            background: rgba(19, 19, 21, 0.4);
            &:hover {
                background: fade(@dv-c-foreground, 40%);
                color:white;
            }
        }
}
</style>