<template>
  <div>
    <h1 class="divided">cash</h1>
    <go-back />
    <tabs ref='cashTab' mode="verticle" defaultTabId="0" @clickTab="getTabInfo">
      <tab v-for="(v,k) in mainTab" :key="k" :title="v.name" :id="v.id">
        <tabs mode="horizon" defaultTabId="0" @clickTab="clicksubtab">
          <tab v-for="(vv,kk) in v.subTab" :key="kk" :title="vv.name" :id="v.id+','+vv.id">
            {{vv.id}}
          </tab>
        </tabs>
      </tab>
    </tabs>
    <cashitem></cashitem>
  </div>
</template>

<script>
import Vue from 'vue';
import cashitem from "./CashItem.vue";
import Tab from "@/components/Tab/Tab";
import Tabs from "@/components/Tab/Tabs";
import GameInfo from "../api/GameInfo";
import Loader from "@/components/util/Loader";
import GoBack from "@/components/util/GoBack.vue";
Vue.component("load-indicator", Loader);
//Vue.component("back-to-top", BackToTop);
export default {
  components: {
    cashitem,
    Tab,
    Tabs,
    GoBack,
  },
  data() {
    return {
      loading: true,
      mainTab: null,
    };
  },
  created(){
    this.getTabInfo();
  },
  methods: {
    async getTabInfo() {
      if (GameInfo.CashTabCache) {
        this.mainTab = GameInfo.CashTabCache;
      } else {
        this.loading = true;
        await GameInfo.CashTab(
          this.$store.state.regionCode,
          (res) => {
            this.$refs.cashTab.selectTab(0,0);
            this.loading = false;
            this.mainTab = res;
          },
          (err) => {
            this.error = err;
            this.loading = false;
          }
        );
      }
    },
    clicksubtab(e){
      console.log(e)
    }
  },
};
</script>

<style></style>
